<template>
  <div id="random">
    <div id="countBar"></div>
    <!-- <div>
            <form>
                <tr>
                    <td><button @click="currentTab = 'PaperMakeRandomSingle'">单选</button></td>
                    <td><button @click="currentTab = 'PaperMakeRandomMultiple'">多选</button></td>
                    <td><button @click="currentTab = 'PaperMakeRandomMix'">单选+多选</button></td>
                </tr>
            </form>
        </div> -->
    <div>
      <p>自动组卷</p>
      <button
        v-for="tab in tabs"
        :key="tab.title"
        :class="['tab-button', { active: currentTab === tab.title }]"
        @click="currentTab = tab.title"
      >
        {{ tab.displayName }}
      </button>
    </div>
    <div id="randomMain">
      <component
        v-bind:is="this.currentTabComponent"
        class="RandomTab"
      ></component>
      <!-- <form>
                总分：<input v-model="WholePoint" type="text">
                <br>
                试题类型：
                <input id="single" type="radio" v-model="PaperType" value="single">
                <label for="single">单选</label>
                <input id="multiple" type="radio" v-model="PaperType" value="multiple">
                <label for="multiple">多选</label>
                <input id="singleAndMultiple" type="radio" v-model="PaperType" value="SingleAndMultiple">
                <label for="SingleAndMultiple">单选与多选</label>
                <br>
                单选总分：<input v-model="SinglePoint" type="text" style="width:50px">
                单选分值：<input v-model="SingleChoicePoint" type="text" style="width:50px">
                单选题数：<input type="text" v-model="SingleChoiceAmount" style="width:50px">
            </form> -->
    </div>
  </div>
</template>
<script>
import PaperMakeRandomSingle from "./PaperMakeRandomSingle";
import PaperMakeRandomMultiple from "./PaperMakeRandomMultiple";
import PaperMakeRandomMix from "./PaperMakeRandomMix";
export default {
  name: "PaperMakeRandom",
  components: {
    PaperMakeRandomSingle,
    PaperMakeRandomMultiple,
    PaperMakeRandomMix,
  },
  data() {
    return {
      currentTab: "Single",
      tabs: [
        { title: "Single", displayName: "单选" },
        { title: "Multiple", displayName: "多选" },
        { title: "Mix", displayName: "单选+多选" },
      ],
    };
  },
  computed: {
    currentTabComponent: function () {
      return "PaperMakeRandom" + this.currentTab;
    },
  },
};
</script>

<style scoped>
    .tab-button{
        padding: 6px 10px;
        border-top-left-radius: 3px;
        border-top-right-radius: 3px;
        border: 1px solid #ccc;
        cursor: pointer;
        background: #f0f0f0;
        margin-bottom: -1px;
        margin-right: -1px;

    }
</style>