<template>
    <div>
        <p>单选</p>
        <div id="SingleHelpBar">
            <form v-show="(Paper.WholePointSingle>0 && Paper.SingleChoiceAmountFull>0)">
                <ul>
                    <li>辅助栏：{{SingleChoiceAmount}}/{{Paper.SingleChoiceAmountFull}}</li>
                </ul>
            </form>
        </div>
         <div id="SingleBar">
             <table>
                 <tr>
                     <td>输入总分：</td>
                     <td><input v-model.number="Paper.WholePointSingle" type="text"></td>
                 </tr>
                 <tr>
                     <td>输入题目数量：</td>
                     <td><input v-model.number="Paper.SingleChoiceAmountFull" type="text"></td>
                 </tr>
                 <tr>
                     <td>单个题目分值：</td>
                     <td>{{SingleChoicePoint}}</td>
                 </tr>
             </table>
        </div>
        <div id="doubletest">
            <div v-for="(option,indexmain) in Paper.options"
                :key="indexmain"
            >
                <tr>
                    <th>{{option.value}}</th>
                    <td>{{option.single}}</td>
                </tr>
                <tr v-for="(optionsub,indexsub) in option.optionsubs"
                    :key="indexsub"
                >
                    <td>{{optionsub.value}}</td>
                    <td><button 
                        :disabled="optionsub.single == 0" 
                        @click="handleSubstract(option.id,optionsub.id,0)"
                        >-</button>
                    </td>
                    <td>{{optionsub.single}}</td>
                    <td><button 
                        :disabled="Paper.SingleChoiceAmount >= Paper.SingleChoiceAmountFull" 
                        @click="handleAdd(option.id,optionsub.id,0)"
                        >+</button></td>
                </tr>
            </div>
            <p><button 
                type="submit" 
                :disabled="!((Paper.SingleChoiceAmount == Paper.SingleChoiceAmountFull)&&(Paper.SingleChoiceAmountFull!=0))" 
                @click="handleSubmitAndPreview(0)"
                >提交并预览
                </button>
            </p>
        </div>
      
        
    </div>
</template>

<script>
import {mapGetters,mapState,mapMutations} from 'vuex'
export default {
    name:'PaperMakeRandomSingle',
    computed:{
        ...mapState('papermakerandom',{
            Paper:'Paper'
        }),
        ...mapGetters('papermakerandom',[
            'SingleChoiceAmount',
            'SingleChoicePoint',
        ])
    },
    methods:{
        ...mapMutations('papermakerandom',[
            'add',
            'substract',
            'handleSubmitAndPreview',
        ]),

        handleAdd(id1,id2,sign){
            this.add({id1:id1,id2:id2,sign:sign});
        },
        handleSubstract(id1,id2,sign){
            this.substract({id1:id1,id2:id2,sign:sign});
        }
    },
  
}
</script>

<style scoped>
    td{
        width:20px,
    }
</style>