<template>
    <div id="MixMain">
        <p>单选+多选</p>
        <div id="MixHelpBar">
            <form v-show="(Paper.WholePointMultiple>0 && Paper.MultipleChoiceAmountFull>0 && Paper.WholePointSingle>0 && Paper.SingleChoiceAmountFull>0)">
                <ul>
                    <li>单选辅助栏：{{SingleChoiceAmount}}/{{Paper.SingleChoiceAmountFull}}</li>
                    <li>多选辅助栏：{{MultipleChoiceAmount}}/{{Paper.MultipleChoiceAmountFull}}</li>
                </ul>
            </form>
        </div>
        <div id="MixBar">
            <table>
                 <tr>
                     <td>输入单选总分：</td>
                     <td><input v-model.number="Paper.WholePointSingle" type="text"></td>
                     <td>输入多选总分：</td>
                     <td><input v-model.number="Paper.WholePointMultiple" type="text"></td>
                 </tr>
                 <tr>
                     <td>输入单选题目数量：</td>
                     <td><input v-model.number="Paper.SingleChoiceAmountFull" type="text"></td>
                     <td>输入多选题目数量：</td>
                     <td><input v-model.number="Paper.MultipleChoiceAmountFull" type="text"></td>
                 </tr>
                 <tr>
                     <td>单选分值：</td>
                     <td>{{SingleChoicePoint}}</td>
                     <td>多选分值：</td>
                     <td>{{MultipleChoicePoint}}</td>
                 </tr>
             </table> 
        </div>
        <div id="MixMain">
            <table 
                border="1"
                width="100%"
                cellpadding="0" 
                cellspacing="0" 
                style="table-layout:fixed"
            >
           
            <tr>
                    <th width="100"></th>
                    <th width="30"></th>
                    <td  width="40" colspan="3">单选数量</td>
                    <th width="30"></th>
                    <th width="30"></th>
                    <th width="40">多选数量</th>
                    <th width="30"></th>
                   
                </tr>
            </table>
            <table 
                border="1"
                width="100%"
                cellpadding="0" 
                cellspacing="0" 
                style="table-layout:fixed"
            >
            <tr>
                    <th width="100">标题</th>
                    <th width="30"></th>
                    <th width="40" colspan="3">单选数量</th>
                    <th width="30"></th>
                    <th width="30"></th>
                    <th width="40">多选数量</th>
                    <th width="30"></th>
                </tr>
            </table>
            <table v-for="(option,indexmain) in Paper.options"
                :key="indexmain"
                border="1"
                width="100%"
                cellpadding="0" 
                cellspacing="0" 
                style="table-layout:fixed"
            >
                <thead>
                <tr>
                    <th width="100">{{option.value}}</th>
                    <th width="30"></th>
                    <th width="40">{{option.single}}</th>
                    <th width="30"></th>
                    <th width="30"></th>
                    <th width="40">{{option.multiple}}</th>
                    <th width="30"></th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(optionsub,indexsub) in option.optionsubs"
                    :key="indexsub"
                >
                    <td>{{optionsub.value}}</td>
                    <td><button 
                        :disabled="optionsub.single == 0" 
                        @click="handleSubstract(option.id,optionsub.id,0)"
                        >-</button>
                    </td>
                    <td>{{optionsub.single}}</td>
                    <td><button 
                        :disabled="Paper.SingleChoiceAmount >= Paper.SingleChoiceAmountFull" 
                        @click="handleAdd(option.id,optionsub.id,0)"
                        >+</button>
                    </td>

                    
                    <td><button 
                        :disabled="optionsub.multiple == 0" 
                        @click="handleSubstract(option.id,optionsub.id,1)"
                        >-</button>
                    </td>
                    <td>{{optionsub.multiple}}</td>
                    <td><button 
                        :disabled="Paper.MultipleChoiceAmount >= Paper.MultipleChoiceAmountFull" 
                        @click="handleAdd(option.id,optionsub.id,1)"
                        >+</button>
                    </td>
                </tr>
                </tbody>
            </table>
           
            <p><button 
                type="submit" 
                :disabled="!( (Paper.SingleChoiceAmount == Paper.SingleChoiceAmountFull) && (Paper.SingleChoiceAmountFull!=0) && (Paper.MultipleChoiceAmount == Paper.MultipleChoiceAmountFull) && (Paper.MultipleChoiceAmountFull!=0) )" 
                @click="handleSubmitAndPreview(2)"
                >提交并预览
                </button>
            </p>
        </div>

    </div>
</template>

<script>
import {mapGetters,mapState,mapMutations} from 'vuex'
export default {
    name:'PaperMakeRandomMix',
    computed:{
        ...mapState('papermakerandom',{
            Paper:'Paper'
        }),
        ...mapGetters('papermakerandom',[
            'SingleChoiceAmount',
            'SingleChoicePoint',
            'MultipleChoiceAmount',
            'MultipleChoicePoint',
        ])
    },
    methods:{
        ...mapMutations('papermakerandom',[
            'add',
            'substract',
            'handleSubmitAndPreview',
        ]),

        handleAdd(id1,id2,sign){
            this.add({id1:id1,id2:id2,sign:sign});
        },
        handleSubstract(id1,id2,sign){
            this.substract({id1:id1,id2:id2,sign:sign});
        }
    },
}
</script>

<style scoped>
   
</style>