<template>
    <div>
        <p>多选</p>
        <div id="MultipleHelpBar">
            <form v-show="(Paper.WholePointMultiple>0 && Paper.MultipleChoiceAmountFull>0)">
                <ul>
                    <li>辅助栏：{{MultipleChoiceAmount}}/{{Paper.MultipleChoiceAmountFull}}</li>
                </ul>
            </form>
        </div>
        <div id="MultipleBar">
           <table>
                 <tr>
                     <td>输入总分：</td>
                     <td><input v-model.number="Paper.WholePointMultiple" type="text"></td>
                 </tr>
                 <tr>
                     <td>输入题目数量：</td>
                     <td><input v-model.number="Paper.MultipleChoiceAmountFull" type="text"></td>
                 </tr>
                 <tr>
                     <td>单个题目分值：</td>
                     <td>{{MultipleChoicePoint}}</td>
                 </tr>
             </table> 
        </div>
        <div id="MultipleMain">
            <div v-for="(option,indexmain) in Paper.options"
                :key="indexmain"
            >
                <tr>
                    <th>{{option.value}}</th>
                    <td>{{option.multiple}}</td>
                </tr>
                <tr v-for="(optionsub,indexsub) in option.optionsubs"
                    :key="indexsub"
                >
                    <td>{{optionsub.value}}</td>
                    <td><button 
                        :disabled="optionsub.multiple == 0" 
                        @click="handleSubstract(option.id,optionsub.id,1)"
                        >-</button>
                    </td>
                    <td>{{optionsub.multiple}}</td>
                    <td><button 
                        :disabled="Paper.MultipleChoiceAmount >= Paper.MultipleChoiceAmountFull" 
                        @click="handleAdd(option.id,optionsub.id,1)"
                        >+</button></td>
                </tr>
            </div>
            <p><button 
                type="submit" 
                :disabled="!((Paper.MultipleChoiceAmount == Paper.MultipleChoiceAmountFull)&&(Paper.MultipleChoiceAmountFull!=0))" 
                @click="handleSubmitAndPreview(1)"
                >提交并预览
                </button>
            </p>
        </div>
    </div>
</template>

<script>
import {mapGetters,mapState,mapMutations} from 'vuex'
export default {
    name:'PaperMakeRandomMultiple',
    computed:{
        ...mapState('papermakerandom',{
            Paper:'Paper'
        }),
        ...mapGetters('papermakerandom',[
            'MultipleChoiceAmount',
            'MultipleChoicePoint',
        ])
    },
    methods:{
        ...mapMutations('papermakerandom',[
            'add',
            'substract',
            'handleSubmitAndPreview',
        ]),

        handleAdd(id1,id2,sign){
            this.add({id1:id1,id2:id2,sign:sign});
        },
        handleSubstract(id1,id2,sign){
            this.substract({id1:id1,id2:id2,sign:sign});
        }
    },
}
</script>